<!-- VueBinary -->
<template>
  <div class="binary" v-show="debug">
    <canvas ref="canvasRef"></canvas>
  </div>
</template>

<script>
export default {
  /* eslint-disable */
  name: "VueBinary",
  props: {
    imgUrl: {
      type: String,
      default: ""
    },
    debug: {
      type: Boolean,
      default: false,
    },
    threshold: { // éå¼ é»è®¤ä¸º0æ¶ä½¿ç¨èªéåºéå¼
      type: Number,
      default: 0,
    },
    thresholdCalculation: { // æ¹åå¨æéå¼
      type: Number,
      default: 0,
    }
  },
  data() {
    return {};
  },
  watch: {
    imgUrl: {
      handler: "initCanvas",
      deep: true,
      immediate: true
    }
  },
  methods: {
    initCanvas() {
      if (this.imgUrl === "") {
        return;
      }
      this.$nextTick(() => {
        let img = new Image();
        //è§£å³è·¨åé®é¢
        img.crossOrigin = "Anonymous";
        img.src = this.imgUrl;
        let canvas;
        let ctx;
        //å¾çå è½½å®æè§¦å
        img.onload = () => {
          canvas = this.$refs.canvasRef;
          canvas.width = img.width;
          canvas.height = img.height;
          ctx = canvas.getContext("2d");
          ctx.drawImage(img, 0, 0, img.width, img.height);
          this.binarization();
        };
      });
    },
    //ä¸ç»´OTSUå¾åå¤çç®æ³
    binarization() {
      /** è¡¨ç¤ºç°åº¦å¼çåå¸ç¹æ¦ç [0: 0.01313, 1: 0.12041] */
      var m_pFstdHistogram = new Array();
      /** å¶ä¸­æ¯ä¸ä¸ªå¼ç­äºm_pFstdHistogramä¸­ä»0å°å½åä¸æ å¼çå */
      var m_pFGrayAccu = new Array();
      /** å¶ä¸­æ¯ä¸å¼ç­äºm_pFstdHistogramä¸­ä»0å°å½åæå®ä¸æ å¼*å¯¹åºçä¸æ ä¹å */
      var m_pFGrayAve = new Array();
      /** å¼ä¸ºm_pFstdHistogramã256ãä¸­æ¯ä¸ç¹çåå¸æ¦ç*å½åä¸æ ä¹å */
      var m_pAverage = 0;
      /** ç°åº¦æä¸ªå¼ 0-255ä¹é´ä¸ªæ° [0:2, 149: 1] */
      var m_pHistogram = new Array(); //ç°åº¦ç´æ¹å¾
      var i, j;
      var temp = 0,
        fMax = 0; //å®ä¹ä¸ä¸ªä¸´æ¶åéåä¸ä¸ªæå¤§ç±»é´æ¹å·®çå¼
      var nThresh = 0; //æä¼éå¼
      //è·åç°åº¦å¾åçä¿¡æ¯
      var imageInfo = this.getGrayImageInfo();
      if (imageInfo == null) {
        return;
      }
      //åå§ååé¡¹åæ°
      for (i = 0; i < 256; i++) {
        m_pFstdHistogram[i] = 0;
        m_pFGrayAccu[i] = 0;
        m_pFGrayAve[i] = 0;
        m_pHistogram[i] = 0;
      }
      //è·åå¾åä¿¡æ¯
      var canvasData = imageInfo[0];
      //è·åå¾åçåç´ 
      var pixels = canvasData.data;
      //ä¸é¢ç»è®¡å¾åçç°åº¦åå¸ä¿¡æ¯
      for (i = 0; i < pixels.length; i += 4) {
        //è·årçåç´ å¼ï¼å ä¸ºç°åº¦å¾åï¼r=g=bï¼æä»¥åç¬¬ä¸ä¸ªå³å¯
        let r = pixels[i];
        m_pHistogram[r]++;
      }
      //ä¸é¢è®¡ç®æ¯ä¸ä¸ªç°åº¦ç¹å¨å¾åä¸­åºç°çæ¦ç
      var size = canvasData.width * canvasData.height;
      for (i = 0; i < 256; i++) {
        m_pFstdHistogram[i] = m_pHistogram[i] / size;
      }
      //ä¸é¢å¼å§è®¡ç®m_pFGrayAccuåm_pFGrayAveåm_pAverageçå¼
      for (i = 0; i < 256; i++) {
        for (j = 0; j <= i; j++) {
          //è®¡ç®m_pFGaryAccu[256]
          m_pFGrayAccu[i] += m_pFstdHistogram[j];
          //è®¡ç®m_pFGrayAve[256]
          m_pFGrayAve[i] += j * m_pFstdHistogram[j];
        }
        //è®¡ç®å¹³åå¼
        m_pAverage += i * m_pFstdHistogram[i];
      }
      //ä¸é¢å¼å§å°±ç®OSTUçå¼ï¼ä»0-255ä¸ªå¼ä¸­åå«è®¡ç®ostuå¹¶å¯»æ¾åºæå¤§å¼ä½ä¸ºåå²éå¼
      for (i = 0; i < 256; i++) {
        // è®¡ç®åæ¯åç´ åèæ¯åç´ çæ¹å·® g = w0*w1*(u0-u1) (u0-u1)
        temp =
          ((m_pAverage * m_pFGrayAccu[i] - m_pFGrayAve[i]) *
          (m_pAverage * m_pFGrayAccu[i] - m_pFGrayAve[i])) /
          (m_pFGrayAccu[i] * (1 - m_pFGrayAccu[i]));
        if (temp > fMax) {
          fMax = temp;
          nThresh = i;
        }
      }
      // å¦æè¾å¥é»è®¤éå¼ åæ¿æ¢èªéåºéå¼
      if (this.threshold !== 0) {
        nThresh = this.threshold;
      } else if (this.thresholdCalculation !== 0) {
        nThresh += this.thresholdCalculation
      }
      console.log(nThresh)
      //ä¸é¢æ§è¡äºå¼åè¿ç¨
      const binaryArr = [];
      for (i = 0; i < canvasData.width; i++) {
        for (j = 0; j < canvasData.height; j++) {
          //åå¾æ¯ä¸ç¹çä½ç½®
          var ids = (i + j * canvasData.width) * 4;
          //åå¾åç´ çRåéçå¼
          let r = canvasData.data[ids];
          //ä¸éå¼è¿è¡æ¯è¾ï¼å¦æå°äºéå¼ï¼é£ä¹å°æ¹ç¹ç½®ä¸º0ï¼å¦åç½®ä¸º255
          var gray = r > nThresh ? 255 : 0;
          // äºè¿å¶ 1è¡¨ç¤ºç»å¶ 0è¡¨ç¤ºç©ºç½
          binaryArr.push(r > nThresh ? 1 : 0);
          canvasData.data[ids + 0] = gray;
          canvasData.data[ids + 1] = gray;
          canvasData.data[ids + 2] = gray;
        }
      }
      //æ¾ç¤ºäºå¼åå¾å
      var newImage = this.$refs.canvasRef.getContext("2d");
      newImage.putImageData(canvasData, 0, 0);
      this.$emit('success', {
        imgBase: this.$refs.canvasRef.toDataURL('image/jpeg'),
        binaryArr: binaryArr.join(''),
        canvasData,
      })
    },

    //è·åå¾åçç°åº¦å¾åçä¿¡æ¯binarization
    getGrayImageInfo() {
      var canvas = this.$refs.canvasRef;
      var ctx = canvas.getContext("2d");
      var canvasData = ctx.getImageData(0, 0, canvas.width, canvas.height);
      if (canvasData.data.length == 0) {
        return null;
      }
      return [canvasData, ctx];
    }
  }
};
</script>
